<template>
  <!-- <div id="echarts" :style="{width: '600px', height: '400px'}"></div> -->
  <!-- <div :id="echartsId" :style="{height:'100%'}"></div> -->
  <div :id="echartsId" :style="{height:'100%', width:'100%'}"></div>
</template>
<script>
export default {
  name: "echartsUtil",
  data() {
    return {
      myChart: null, //
      optionData: null //
      // chartHeight:document.getElementById(this.echartsId).height(),
    };
  },
  props: {
    //echartsid,
    echartsId: {
      type: String,
      required: true,
      default: "echarts"
    },
    // \n 
    title: {
      type: String,
      required: false,
      default: null
    },
    //
    titleShow: {
      type: Boolean,
      default: false
    },
    //
    tooltipShow: {
      type: Boolean,
      default: true
    },
    //
    tooltipTrigger: {
      type: String,
      default: "axis"
    },
    //
    legendData: {
      type: Array,
      required: false
    },
    //
    useAxis: {
      type: Boolean,
      default: false
    },
    //x
    xAxisName: {
      type: String,
      required: false
    },
    //x
    xAxisType: {
      type: String,
      default: "category"
    },
    //xtype: 'category'
    xAxisData: {
      type: Array,
      required: false
    },
    //y
    yAxisType: {
      type: String,
      default: "value"
    },
    //y
    yAxisName: {
      type: String,
      required: false
    },
    //y
    yAxisPosition: {
      type: String,
      default: "left"
    },
    //y
    yAxisNameLocation: {
      type: String,
      default: "end"
    },
    //
    yAxisFormatter: {
      type: Function,
      required: false
    },
    // type 
    seriesType: {
      type: String,
      default: "line",
      required: false
    },
    //
    titleParam: {
      type: Object,
      default: null
    },
    //
    colorParam: {
      type: Array,
      default: null
    },
    //
    tooltipParam: {
      type: Object,
      default: null
    },
    //
    axisPointerParam: {
      type: Object,
      default: null
    },
    //
    legendParam: {
      type: Object,
      default: null
    },
    //
    legendShow: {
      type: Boolean,
      default: true
    },
    //
    gridParam: {
      type: Object,
      default: null
    },
    // grid  x 
    xAxisParam: {
      type: Object,
      default: null
    },
    // grid  y 
    yAxisParam: {
      type: Array,
      default: null
    },
    // type 
    seriesParam: {
      type: Array,
      default: () => []
    },
    // symbol(),
    seriesSymbolShow: {
      type: Boolean,
      default: true
    },
    //tooltiplegend  setOption 
    seriesName: {
      type: String,
      default: null
    },
    // ID
    seriesId: {
      type: String,
      default: null
    },
    //
    seriesData: {
      type: Array,
      default: () => []
    },
    //
    seriesLabelShow: {
      type: Boolean,
      default: false
    },
    //
    seriesRoseType: {
      type: String,
      default: null
    },
    //
    seriesRadius: {
      type: Array,
      default: null
    },
    //y
    yNameGap: {
      type: Number,
      default: 13
    },
    //
    legendBottom: {
      type: Number,
      default: 0
    },
    //
    legendItemWidth: {
      type: Number,
      default: 15
    },
    //
    legendItemHeight: {
      type: Number,
      default: 7
    },
    //grid 
    gridLeft: {
      type: Number,
      default: 20
    }
  },
  created: function() {
    this.addWatchToProps();
  },
  mounted: function() {
    this.initEchart(this.echartsId);
    //id,
    this.$nextTick(() => {
      this.optionOpt();
    });
  },
  watch: {
    // echartsId: {
    //   deep: true,
    //   handler(val, oldval) {
    //     this.optionOpt();
    //   }
    // }
  },
  computed: {
    titleValue() {
      if (this.titleParam != null) {
        return this.titleParam;
      } else {
        let title = {
          //
          show: this.titleShow,
          // \n 
          text: this.title
        };
        return title;
      }
    },
    colorValue() {
      if (this.colorParam != null) {
        return this.colorParam;
      } else {
        let color = [
          //"#ff8315",
          "#00c0ff",
          "#ffae68",
          "#f8c516",
          "#ff1dc1",
          "#4000ff",
          "#01a63d",
          "#de4343",
          "#0273ff",
          "#9933cc",
          "#9d2d1f",
          "#f8c209",
          "#00bbc7",
          "#7249ee",
          "#005d84",
          "#6ca200",
          "#ff7bdb"
        ];
        return color;
      }
    },
    tooltipValue() {
      if (this.tooltipParam != null) {
        return this.tooltipParam;
      } else {
        let tooltip = {
          show: this.tooltipShow,
          //
          trigger: this.tooltipTrigger,
          //
          backgroundColor: "rgba(0,0,0,0.7)",
          //px5
          padding: 10,
          //
          textStyle: {
            color: "#ffffff",
            fontSize: 12
          },
          //
          axisPointer: {
            type: "line",
            z: 1,
            lineStyle: {
              color: "#ffe329"
            }
          }
        };
        return tooltip;
      }
    },
    axisPointerValue() {
      if (this.axisPointerParam != null) {
        return this.axisPointerParam;
      } else {
        let axisPointer = {
          label: {
            color: "#ffffff",
            backgroundColor: "rgba(0,0,0,.7)"
          }
        };
        return axisPointer;
      }
    },
    legendValue() {
      if (this.legendParam != null) {
        return this.legendParam;
      } else {
        let legend = {
          data: this.legendData, // name name
          //'plain''scroll'
          type: "scroll",
          //
          show: this.legendShow,
          //
          bottom: this.legendBottom,
          // icon
          // ECharts  'circle', 'rect', 'roundRect', 'triangle', 'diamond', 'pin', 'arrow', 'none'
          // icon: "roundRect",
          //
          itemWidth: this.legendItemWidth,
          //
          itemHeight: this.legendItemHeight,
          //
          inactiveColor: "#ccc",
          //
          textStyle: {
            //
            color: "#888888",
            //
            fontSize: 12,
            //
            lineHeight: 1
          }
          //
        };
        return legend;
      }
    },
    gridValue() {
      if (this.gridParam != null) {
        return this.gridParam;
      } else {
        let grid = {
          //grid 
          top: 10,
          //grid 
          bottom: 40,
          //grid 
          //left  20  '20%'  'left', 'center', 'right'
          // left 'left', 'center', 'right'
          left: this.gridLeft,
          //grid 
          right: 10,
          //grid 
          containLabel: true
        };
        return grid;
      }
    },
    xAxisValue() {
      if (this.xAxisParam != null) {
        return this.xAxisParam;
      } else {
        let xAxis = {
          //
          name: this.xAxisName,
          //
          nameLocation: "end",
          //
          nameGap: 0,
          //
          nameTextStyle: {
            color: "#4e4e4e",
            fontSize: 12
          },
          //
          // 'value' 
          // 'category'  data 
          // 'time' 
          // 'log' 
          type: this.xAxisType,
          //
          axisTick: {
            // boundaryGap  true 
            alignWithLabel: true,
            //
            lineStyle: {
              color: "#eeeeee"
            }
          },
          //
          axisLine: {
            //
            show: true,
            //
            lineStyle: {
              type: "solid",
              color: "#d0d0d0"
            }
          },
          //
          axisLabel: {
            color: "#888888",
            fontSize: 11,
            align: "center"
          },
          // grid 
          splitLine: {
            //
            show: true,
            lineStyle: {
              type: "dotted",
              color: "#d8d8d8"
            }
          },
          //type: 'category'
          //  type axis.data type  'category'
          data: this.xAxisData
        };
        return xAxis;
      }
    },
    yAxisValue() {
      if (this.yAxisParam != null) {
        return this.yAxisParam;
      } else {
        let yAxis = [
          //y
          {
            //
            type: this.yAxisType,
            //
            name: this.yAxisName,
            //
            nameGap: this.yNameGap,
            //
            nameLocation: this.yAxisNameLocation,
            //y 
            position: this.yAxisPosition,
            //
            axisLine: {
              //
              show: true,
              //
              lineStyle: {
                color: "#d0d0d0"
              }
            },
            //
            axisLabel: {
              //
              formatter: this.yAxisFormatter,
              color: "#888888",
              fontSize: 11,
              margin: 10
            },
            //
            axisTick: {
              //
              lineStyle: {
                color: "#eeeeee"
              }
            },
            // grid 
            splitLine: {
              //
              show: true,
              lineStyle: {
                type: "dotted",
                color: "#d8d8d8"
              }
            },
            //
            nameTextStyle: {
              color: "#888888",
              fontSize: 12,
              align: "left"
            }
          }
        ];
        return yAxis;
      }
    },
    seriesValue() {
      let data = this.seriesParam;
      if (data.length > 0) {
        return data;
      } else {
        let series = [
          {
            //
            type: this.seriesType,
            id: this.seriesId,
            name: this.seriesName,
            data: this.seriesData,
            symbol: "emptyCircle",
            symbolSize: 6,
            showSymbol: this.seriesSymbolShow,
            cursor: "pointer",
            color: this.colorParam,
            radius: this.seriesRadius,
            roseType: this.seriesRoseType,
            labelLine: {
              normal: {
                length: 5,
                length2: 5
              }
            },
            label: {
              normal: {
                formatter: "{d}% {c}"
              },
              show: this.seriesLabelShow
            }
          }
        ];
        return series;
      }
    }
  },
  methods: {
    initEchart(value) {
      // domecharts
      this.myChart = echarts.init(document.getElementById(value));
      //Echrts - 
      var _this = this;
      window.addEventListener("resize", function() {
        _this.myChart.resize();
      });
    },
    optionOpt() {
      if (this.myChart) {
        // 
        this.optionData = {
          //
          title: this.titleValue,
          //
          color: this.colorValue,
          //
          tooltip: this.tooltipValue,
          //axisPointer
          axisPointer: this.axisPointerValue,
          //(symbol)
          //
          legend: this.legendValue,
          //
          grid: this.gridValue,
          // grid  x  grid  x 
          // x  offset  x 
          // xAxis: this.xAxisValue,
          // yAxis: this.yAxisValue,
          // type 
          series: this.seriesValue
        };

        //,
        if (this.useAxis) {
          this.optionData["xAxis"] = this.xAxisValue;
          this.optionData["yAxis"] = this.yAxisValue;
        }
        this.optionInit();
      }
    },
    optionInit() {
      // 
      this.myChart.resize();
      this.myChart.setOption(this.optionData);
    },
    addWatchToProps() {
      var _this3 = this;

      var watchedVariable = this._watchers.map(function(watcher) {
        return watcher.expression;
      });
      Object.keys(this.$props).forEach(function(prop) {
        if (!~watchedVariable.indexOf(prop)) {
          var opts = {};
          if (
            ~["[object Object]", "[object Array]"].indexOf(
              _this3.getType(_this3.$props[prop])
            )
          ) {
            opts.deep = true;
          }
          _this3.$watch(
            prop,
            function() {
              _this3.optionOpt();
            },
            opts
          );
        }
      });
    },
    getType(v) {
      return Object.prototype.toString.call(v);
    },
    resizeOption() {
      this.myChart.resize();
    }
  }
};
</script>


