import axios from 'axios'
import qs from 'qs'
import { Message, Loading } from 'element-ui'

// api
// axios.defaults.baseURL = process.env.baseUrl
// 
// if (process.env.NODE_ENV === 'development') {      // 
//   axios.defaults.baseURL  = '/api'
// } else {                                           // 
//   if (process.env.type === 'test') { // 
//     axios.defaults.baseURL  = Glob.BaseUrl  // 
//   } else {   //    
var domain = document.domain
var port = window.location.port                            
    // axios.defaults.baseURL  =  port  //  
//   }
// }

axios.defaults.timeout = 5 * 100000
// 
axios.defaults.headers.post['Content-Type'] = 'application/x-www-form-urlencoded;charset=UTF-8'
var loadingInstance

// POST()
axios.interceptors.request.use(
  config => {
    loadingInstance = Loading.service({
      lock: true,
      text: '...',
      spinner: 'icon-custom-loading',
      background: 'rgba(0, 0, 0, 0.7)'
    })
    if (config.method === 'post') {
      // config.data = qs.stringify(config.data)
      //  config.data = JSON.stringify(config.data)
    }
    return config
  }, err => {
    loadingInstance.close()
    Message.error('')
    return Promise.reject(err)
  })

// ()
axios.interceptors.response.use(res => {
  if (res.status === 200) {
    loadingInstance.close()
    return res
  } else {
    if (res.data.code) {
      loadingInstance.close()
      Message.error(res.data.msg)
    } else { // code data
      if (res.status === 200 && res.request.responseURL.indexOf('cas/login') > 0) {
        let url = process.env.casLoginUrl + '?service=' + process.env.webUrl
        location.href = url
        return
      }
      loadingInstance.close()
      return res.data
    }
  }
}, err => {
  loadingInstance.close()
  Message.error('')
  return Promise.reject(err)
})

// 
export function fetchPost (url, params) {
  return new Promise((resolve, reject) => {
    axios
      .post(url, params)
      .then(
        res => {
          resolve(res.data)
        },
        err => {
          reject(err.data)
        }
      )
      .catch(err => {
        reject(err.data)
      })
  })
}
export function fetchGet (url, params) {
  return new Promise((resolve, reject) => {
    axios
      .get(url, {
        params: params
      })
      .then(res => {
        resolve(res.data)
      })
      .catch(err => {
        reject(err.data)
      })
  })
}

export default axios



