class Stack {
  dataStore= [];
  top= 0;         //
  pop() {
    return this.dataStore[--this.top];
  };        //
  push(element) {
    this.dataStore[this.top++] = element;
  };    //

  // top - 1 
   peek() {
    if (this.top > 0) return this.dataStore[this.top - 1];
    else return 'Empty';
  };      //

  // top 
   length() {
    return this.top;
  };   //

  // top  0  dataStore 
   clear() {
    delete this.dataStore;
    this.dataStore = [];
    this.top = 0;
  }
}


export default Stack


