<template>
  <div>
    <el-tree
  :data="data2"
  show-checkbox
  default-expand-all
  node-key="id"
  ref="tree"
  highlight-current
  :props="defaultProps">
</el-tree>

<div class="buttons">
  <el-button @click="getCheckedNodes"></el-button>
  <el-button @click="resetChecked"></el-button>
</div>
  </div>
  </template>
  <script>
  export default {
   methods: {
      getCheckedNodes() {
        var rad=''
        var ridsa = this.$refs.tree.getCheckedKeys().join(',')// [] -id, 
        var ridsb = this.$refs.tree.getCheckedNodes()// {}
        ridsb.forEach(ids=>{//id
          rad+=','+ids.pid
        })
        rad=rad.substr(1) // ','
        var rids=rad+','+ridsa
        var arr=rids.split(',')//  
        arr=[...new Set(arr)]; // 
        rids=arr.join(',')// 
        console.log(rids)
      },
      resetChecked() {
        this.$refs.tree.setCheckedKeys([]);
      }
    },

    data() {
      return {
        data2: [{
          pid:0,
          path:xxxx,
          id: 1,
          label: ' 1',
          children: [{
            pid:1,
            path:xxxx,
            id: 11,
            label: ' 1-1'
          },
          {
            pid:1,
            path:xxxx,
            id: 12,
            label: ' 1-2'
          },
          {
            pid:1,
            path:xxxx,
            id: 13,
            label: ' 1-3'
          }]
        }],
        defaultProps: {
          children: 'children',
          label: 'label'
        }
      };
    }
  };
</script>
  </script>
  <style scoped>
  </style>


