<template>
  <div class="mainBox">
    <div class="el-row dashboard">
      <div class="el-col el-col-24 page_content_top">
        <div class="left pull-left">
          <i class="icon-cluster-host"></i>
          <span class="host-state">
            (
            <span class="countNum">{{sumNumber}}</span>
            <span class="abnormalNum">{{errorNum}}</span>)
          </span>
          <span class="healthy"></span>
          <span class="unhealthy"></span>
          <span class="drop-line"></span>
        </div>
        <div class="right pull-right">
          <el-tooltip class="item" content="" placement="bottom">
            <span class="refresh">
              <span class="icon-refresh" @click="refreshOperate()"></span>
            </span>
          </el-tooltip>
          <span class="update-time"></span>
          <span class="time">{{updateTime}}</span>
        </div>
      </div>
      <div class="el-col el-col-24 page_content_cont">
        <div class="el-col el-col-24 content_top">
          <template v-for="item in sumNode">
            <el-card class="box-card" :class="healthCheck(item.state)">
              <div slot="header" class="clearfix">
                <span>{{item.hostName}}</span>
              </div>
              <div class="text item">
                <p class="text-con">
                  <span class="text-name">CPU</span>
                  <el-progress :percentage="numberFormate(item.cpuUsage)" :stroke-width="11"></el-progress>
                </p>
                <p class="text-con">
                  <span class="text-name"></span>
                  <el-progress :percentage="numberFormate(item.memUsage)" :stroke-width="11"></el-progress>
                </p>
                <p class="text-con">
                  <span class="text-name">()</span>
                  <el-progress :percentage="numberFormate(item.brfsDiskUsage)" :stroke-width="11"></el-progress>
                </p>
                <p class="text-con">
                  <span class="text-name">()</span>
                  <el-progress :percentage="numberFormate(item.systemDiskUsage)" :stroke-width="11"></el-progress>
                </p>
                <p class="text-con">
                  <span class="text-name">regionnode</span>
                  <span class="normal"></span>
                </p>
                <p class="text-con">
                  <span class="text-name">datanode</span>
                  <span class="offLine"></span>
                </p>
              </div>
            </el-card>
          </template>
        </div>
        <div class="el-col el-col-24 content_bottom">
          <!--  -->
          <el-card class="box-card allNode">
            <div slot="header" class="clearfix">
              <span class="titleName"></span>
            </div>
            <div class="text chart-box">
              <v-echarts
                ref="chart"
                :seriesType="seriesType"
                :seriesData="seriesData"
                :echartsId="echarts"
                :tooltipTrigger="tooltipTrigger"
                :seriesLabelShow="true"
                :legendShow="true"
                :seriesRoseType="seriesRoseType"
                :colorParam="colorParam"
                :seriesRadius="seriesRadius"
                :legendBottom="legendBottom"
                :legendItemWidth="legendItemWidth"
                :legendItemHeight="legendItemHeight"
              ></v-echarts>
            </div>
          </el-card>
          <!--  -->
          <el-card class="box-card read-count">
            <div slot="header" class="clearfix">
              <span class="titleName"></span>
              <div class="timeBox">
                <div class="bgtime" :class="sMinutes=='h'?'select':null">
                  <span class="time" @click="hourSelect()">1h</span>
                </div>
                <div class="bgtime" :class="sMinutes=='d'?'select':null">
                  <span class="time" @click="dateSelect()">1D</span>
                </div>
                <div class="bgtime" :class="sMinutes=='td'?'select':null">
                  <span class="time" @click="threeDSelect()">3D</span>
                </div>
              </div>
            </div>
            <div class="text chart-box">
              <div class="chart-cont">
                <!-- <div class="line-chart-cont">
                  <div class="title-box">
                      <span class="ytitle">Root</span>
                  </div>
                  <div class="line-chart">
                    <v-echarts
                      ref="sumChart"
                      :echartsId="sumChart"
                      :seriesParam="rootSeriesParam"
                      :xAxisData="rootTimeData"
                      :useAxis="true"
                      :yNameGap="yNameGap"
                    ></v-echarts>
                  </div>
                </div>-->
                <template v-for="item in listImageData">
                  <div class="line-chart-cont">
                    <div class="title-box">
                      <span class="ytitle">Root{{item.yAxisName}}</span>
                    </div>
                    <div class="line-chart">
                      <v-echarts
                        ref="bussType"
                        :echartsId="item.echartsId"
                        :seriesParam="item.seriesParam"
                        :xAxisData="item.xAxisData"
                        :useAxis="true"
                        :yNameGap="yNameGap"
                      ></v-echarts>
                    </div>
                  </div>
                </template>
              </div>
            </div>
          </el-card>
        </div>
      </div>
    </div>
  </div>
</template>
<script>
import Echarts from "../../utils/echartsUtil";
export default {
  name: "Dashboard",
  components: {
    "v-echarts": Echarts
  },
  data() {
    return {
      sumNode: [], //
      diskNum: null, //
      echarts: "echarts",
      seriesType: "pie", //
      seriesData: [], //
      sumNumber: 0, //
      errorNum: 0, //
      minutes: 60, //()
      sMinutes: "h", //
      rootTimeData: [], //
      rootSeriesParam: [], //rootseries
      sumChart: "sumChart", //id
      // rootYName: "ROOT", //y
      businessList: [], //
      listImageData: [], //
      updateTime: null, //
      tooltipTrigger: "item", //
      seriesRoseType: "radius", //
      colorParam: ["#de4343", "#474747"], //
      seriesRadius: ["10%", "50%"], //
      yNameGap: 13, //y
      legendBottom: 340, //
      legendItemWidth: 10, //
      legendItemHeight: 10, //
      temChartList: {} //
    };
  },
  mounted() {
    this.getSummary();
    this.diskusage();
    this.getSumReadWrite();
  },
  watch: {
    listImageData: {
      deep: true,
      handler(val, oldval) {
        if (this.listImageData.length == this.businessList.length + 1) {
          this.$nextTick(() => {
            this.$refs.bussType[0].resizeOption();
            var value = this.businessList;
            var s = this.$refs.bussType;
            if (
              this.$refs.bussType &&
              value.length == (this.$refs.bussType.length-1)
            ) {
              for (var j in value) {
                this.$refs.bussType[j].resizeOption();
              }
            }
          });
        }
      }
    }
  },
  methods: {
    //DashBoard
    getSummary() {
      this.updateTime = this.$tools.getCurrentTime();
      this.$get("dashboard/summary", null)
        .then(res => {
          this.sumNode = res;
          this.sumNumber = this.sumNode.length;
          var errorNum = 0;
          for (var i in res) {
            if (res[i].state != "ONLINE") {
              errorNum++;
            }
          }
          this.errorNum = errorNum;
        })
        .catch(() => {});
    },
    //
    healthCheck(value) {
      //stateONLINEOFFLINEALERT
      //
      //class: healthy-box,drop-line-box,unhealthy-box
      if (value == "ONLINE") {
        return "healthy-box";
      } else if (value == "OFFLINE") {
        return "drop-line-box";
      } else if (value == "ALERT") {
        return "unhealthy-box";
      }
    },
    //
    numberFormate(value) {
      return Number(value.toFixed(2));
    },
    //
    diskusage() {
      this.seriesData = [];
      this.$get("dashboard/diskusage", null)
        .then(res => {
          this.diskNum = res;
          var free = this.$tools.formatBytes(res.free, 2);
          var used = this.$tools.formatBytes(res.used, 2);
          var parmOfUsed = {
            name: "",
            value: used.value
          };
          var parmOfFree = {
            name: "",
            value: free.value
          };
          this.seriesData.push(parmOfUsed);
          this.seriesData.push(parmOfFree);
        })
        .catch(() => {});
    },
    //
    getSumReadWrite() {
      this.rootSeriesParam = [];
      this.rootTimeData = [];
      var param = {
        minutes: this.minutes
      };
      this.$get("dashboard/stats", param)
        .then(res => {
          var datas = res.datas;
          let rootReadData = [];
          let rootWriteData = [];
          for (var i in datas) {
            var time = this.$tools.getTime(datas[i].time);
            this.rootTimeData.push(time);
            rootReadData.push(datas[i].data.read);
            rootWriteData.push(datas[i].data.write);
          }
          this.rootSeriesParam = [
            {
              //
              type: "line",
              name: "read",
              data: rootReadData,
              symbol: "emptyCircle",
              symbolSize: 6,
              showSymbol: true,
              cursor: "pointer",
              color: "#00c0ff"
            },
            {
              //
              type: "line",
              name: "write",
              data: rootWriteData,
              symbol: "emptyCircle",
              symbolSize: 6,
              showSymbol: true,
              cursor: "pointer",
              color: "#f8c516"
            }
          ];
          this.listBusiness();
        })
        .catch(() => {});
    },
    //
    hourSelect() {
      this.minutes = 60;
      this.sMinutes = "h";
      this.getSumReadWrite();
    },
    //
    dateSelect() {
      this.minutes = 60 * 24;
      this.sMinutes = "d";
      this.getSumReadWrite();
    },
    //
    threeDSelect() {
      this.minutes = 60 * 24 * 3;
      this.sMinutes = "td";
      this.getSumReadWrite();
    },
    //
    listBusiness() {
      this.$get("dashboard/business", null)
        .then(res => {
          this.businessList = res;
          this.listMesgByType(res);
        })
        .catch(() => {});
    },
    //
    listMesgByType(value) {
      var _this = this;
      _this.listImageData = [];
      var d = _this.rootSeriesParam;
      var params = {
        echartsId: "sumChart",
        seriesParam: _this.rootSeriesParam,
        xAxisData: _this.rootTimeData,
        yAxisName: ""
      };
      _this.listImageData.push(params);
      for (var i in value) {
        var param = {
          minutes: _this.minutes
        };
        _this.listReadWriteByType(value[i], param);
      }
    },
    //
    listReadWriteByType(type, param) {
      this.$get("dashboard/stats/" + type, param)
        .then(res => {
          this.drawImageByType(res, type);
        })
        .catch(() => {});
    },
    //
    drawImageByType(value, type) {
      var _this = this;
      var datas = value.datas;
      let timeData = [];
      let readData = [];
      let writeData = [];
      for (var i in datas) {
        var time = _this.$tools.getTime(datas[i].time);
        timeData.push(time);
        readData.push(datas[i].data.read);
        writeData.push(datas[i].data.write);
      }
      let seriesParam = [
        {
          //
          type: "line",
          name: "read",
          data: readData,
          symbol: "emptyCircle",
          symbolSize: 6,
          showSymbol: true,
          cursor: "pointer",
          color: "#00c0ff"
        },
        {
          //
          type: "line",
          name: "write",
          data: writeData,
          symbol: "emptyCircle",
          symbolSize: 6,
          showSymbol: true,
          cursor: "pointer",
          color: "#f8c516"
        }
      ];
      var param = {
        echartsId: value.business,
        seriesParam: seriesParam,
        xAxisData: timeData,
        yAxisName: "/" + value.business
      };
      _this.listImageData.push(param);
    },
    //
    refreshOperate() {
      this.getSummary();
      this.diskusage();
      this.getSumReadWrite();
    }
  }
};
</script>


