export default {
  // deepClone(target) {
  //     return JSON.parse(JSON.stringify(target))
  // }
  //   target
  deepClone(target) {
    // 
    let result;
    // 
    if (typeof target === 'object') {
      // 
      if (Array.isArray(target)) {
        result = []; // result
        for (let i in target) {
          // 
          result.push(this.deepClone(target[i]))
        }
        // nullnull
      } else if (target === null) {
        result = null;
        // RegExp    
      } else if (target.constructor === RegExp) {
        result = target;
      } else {
        // for in
        result = {};
        for (let i in target) {
          result[i] = this.deepClone(target[i]);
        }
      }
      // 
    } else {
      result = target;
    }
    // 
    return result;
  },

  validStrong(param) {
    var newPassword = param.newPassword;
    //
    var regex = /(?!^(\d+|[a-zA-Z]+|[`~!@#$%^&*()_\-+=<>?:"{}|,.\/;'[\]]+)$)^[\w`~!@#\\\\$%^&*()_\-+=<>?:"{}|,.\/;'[\]]{1,50}$/;
    //
    var regexNum = /^[0-9]*$/;
    //
    var regexChar = /^[A-Za-z]+$/;
    //
    var regexSpecial = /[`~!@#$%^&*()_\-+=<>?:"{}|,.\/;'[\]]+$/;

    // var regex=/[a-z0-9A-Z]|[`~!@#$%^&*()_\-+=<>?:"{}|,.\/;'[\]]/;

    var l = newPassword.length;
    if (!newPassword) {
      param['passwordErrorMsg'] = "";
      param['weak'] = "";
      param['center'] = "";
      param['strong'] = "";
      param['password_strength'] = false;
      param['status'] = false;
      return param;
    }
    // if (!regex.test(this.newPassword)) {
    //   this.passwordErrorMsg = "++";
    //   this.password_strength = false;
    //   return false;
    // } else {
    if (l < 10) {
      // this.passwordType = ": ";
      param['passwordErrorMsg'] = "";
      param['weak'] = "weak";
      param['center'] = "";
      param['strong'] = "";
      param['password_strength'] = false;
      param['status'] = false;
      return param;
    }
    if (l >= 10 && l <= 12) {
      if (!regex.test(newPassword)) {
        //
        // this.passwordType = ": ";
        param['passwordErrorMsg'] = "++,";
        param['weak'] = "weak";
        param['center'] = "";
        param['strong'] = "";
        param['password_strength'] = false;
        param['status'] = false;
        return param;
      } else {
        // this.passwordType = ": ";
        param['passwordErrorMsg'] = "";
        param['weak'] = "";
        param['center'] = "center";
        param['strong'] = "";
        param['password_strength'] = true;
        param['status'] = true;
        return param;
      }
    }
    if (l > 12) {
      if (!regex.test(newPassword)) {
        //
        // this.passwordType = ": ";
        param['passwordErrorMsg'] = "++,";
        param['weak'] = "weak";
        param['center'] = "";
        param['strong'] = "";
        param['password_strength'] = false;
        param['status'] = false;
        return param;
      } else {
        // this.passwordType = " : ";
        param['passwordErrorMsg'] = "";
        param['weak'] = "";
        param['center'] = "";
        param['strong'] = "strong";
        param['password_strength'] = true;
        param['status'] = true;
        return param;
      }
    }
    // }
  },
  //KB,a,b
  formatBytes(a, b) {
    if (0 == a) {
      return "0 Bytes";
    }
    var c = 1024,
    d = b || 2,
    e = ["Bytes", "KB", "MB", "GB", "TB", "PB", "EB", "ZB", "YB"],
    f = Math.floor(Math.log(a) / Math.log(c));
    var num={
      "value":parseFloat((a / Math.pow(c, f)).toFixed(d)),
      "unit":e[f]
    }
    // return parseFloat((a / Math.pow(c, f)).toFixed(d)) + " " + e[f];
    return num;
  },
  //
  getTime(timeLong) {
    var now = new Date(parseFloat(timeLong));
    var year = now.getFullYear();
    var month = now.getMonth() + 1;
    month = (month + "").length == 1 ? "0" + month : month;
    var date = now.getDate();
    date = (date + "").length == 1 ? "0" + date : date;
    var hour = now.getHours();
    hour = (hour + "").length == 1 ? "0" + hour : hour;
    var minute = now.getMinutes();
    minute = (minute + "").length == 1 ? "0" + minute : minute;
    var second = now.getSeconds();
    second = (second + "").length == 1 ? "0" + second : second;
    // return year + "-" + month+"-" + date +" "+ hour +":"+ minute+":" + second;
    // return year + "-" + month+"-" + date +" "+ hour +":"+ minute;
    return hour +":"+ minute;
  },
  //
  getCurrentTime() {
    var now = new Date();
    var year = now.getFullYear();
    var month = now.getMonth() + 1;
    month = (month + "").length == 1 ? "0" + month : month;
    var date = now.getDate();
    date = (date + "").length == 1 ? "0" + date : date;
    var hour = now.getHours();
    hour = (hour + "").length == 1 ? "0" + hour : hour;
    var minute = now.getMinutes();
    minute = (minute + "").length == 1 ? "0" + minute : minute;
    var second = now.getSeconds();
    second = (second + "").length == 1 ? "0" + second : second;
    return year + "-" + month+"-" + date +" "+ hour +":"+ minute+":" + second;
    // return year + "-" + month+"-" + date +" "+ hour +":"+ minute;
    // return hour +":"+ minute;
  },
};



